import React from 'react';

export default class HourlyCard extends React.Component {
	render(){
		
		const cardFormat = {
			display: "flex",
			height: "65px",
			border: "solid",
			borderRadius: "14px",
			justifyContent: "center"
		}
		
		const timeFormat = {
			display: "flex",
			justifyContent: "center",
			flexDirection: "column",
			width: "65px",
			textAlign: "center",
		}
		
		const tempWeatherFormat = {
			display: "flex",
			justifyContent: "center",
			width: "70px",
			textAlign: "center",
			flexDirection: "column",
			align: "center"
		}
		
		const conditionFormat = {
			display: "flex",
			justifyContent: "center",
			flexDirection: "column",
			width: "65px",
			textAlign: "center",
		}
		
		const weatherCardFormat = {
			align: "center"
		}
		
		return(
			<div style={cardFormat}>
				<div style={timeFormat}>{this.props.time}</div>
				<div style={tempWeatherFormat}>
					{this.props.temp}
					<div style={weatherCardFormat}>{this.props.weather}</div>
				</div>
				<div style={conditionFormat}>{this.props.condition}</div>
			</div>
		);
	}
}