import React from 'react'
import HourlyCard from './hourlyCard';
import { WiDaySunny, WiRain, WiCloudy, WiDayWindy, WiSnow, WiThunderstorm, WiHail } from 'weather-icons-react';


export default class HourlyForcast extends React.Component {
	render(){
		
		const weatherAppContainer = {
			width: "200px",
			height: "600px",
		}
		
		return(
			<div style={weatherAppContainer}>
				<HourlyCard time="1am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="2am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="3am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="4am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="5am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="6am" weather={<WiSnow size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="7am" weather={<WiDaySunny size={24} />} temp="7" condition="Sunny"/>
				<HourlyCard time="8am" weather={<WiDaySunny size={24} />} temp="7" condition="Sunny"/>
				<HourlyCard time="9am" weather={<WiDaySunny size={24} />} temp="7" condition="Sunny"/>
				<HourlyCard time="10am" weather={<WiDaySunny size={24} />} temp="7" condition="Sunny"/>
				<HourlyCard time="11am" weather={<WiDaySunny size={24} />} temp="7" condition="Snowy"/>
				<HourlyCard time="12am" weather={<WiDaySunny size={24} />} temp="7" condition="Snowy"/>
			</div>
		);
	}
}