import React from 'react';
import FiveDayCard from './fiveDayCard';
import { WiDaySunny, WiRain, WiCloudy, WiDayWindy, WiSnow, WiThunderstorm, WiHail } from 'weather-icons-react';

export default class FiveDayForcast extends React.Component {
	render() {
		
		const weatherAppContainer = {
			display: "flex",
			width: "700px",
			height: "100px",
		}
		
		return (
		<div style={weatherAppContainer}>
			<FiveDayCard date="Monday" weather={<WiDaySunny size={24} />} high="13" low="12"/>
			<FiveDayCard date="Tuesday" weather={<WiDaySunny size={24} />} high="21" low="12"/>
			<FiveDayCard date="Wednesday" weather={<WiDaySunny size={24} />} high="12" low="7"/>
			<FiveDayCard date="Thursday" weather={<WiDaySunny size={24} />} high="24" low="20"/>
			<FiveDayCard date="Friday" weather={<WiDaySunny size={24} />} high="35" low="15"/>
		</div>
		);
	}
}