import React from 'react';
import { Route, Link } from 'react-router-dom';
import FiveDayForcast from './Components/fiveDayForcast';
import SevenDayForcast from './Components/sevenDayForcast';
import HourlyForcast from './Components/hourlyForcast';


export default class App extends React.Component {
  render(){
    return (               
				<div>
					<ul>
						<li><Link to='/7DayForcast'>SevenDayForcast</Link></li>
						<li><Link to='/5DayForcast'>FiveDayForcast</Link></li>
						<li><Link to='/hourlyForcast'>HourlyForcast</Link></li>
					</ul>
				
				<Route path='/7DayForcast' component={SevenDayForcast} />
				<Route path='/5DayForcast' component={FiveDayForcast} />
				<Route path='/hourlyForcast' component={HourlyForcast} />
				</div>
    );
  }
}


